.app_main {
  display: flex;
  justify-content: space-between;
}

.app_login {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app_login>button{
  border: none;
  font-size: 24px;    
  background-color: rgb(67, 130, 244);
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  transition: all 0.2s;
}

.app_login>button:hover{
  cursor: pointer;
  background-color: rgb(49, 94, 179);
  transform: scale(1.1);
}